.container {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  margin: 8px;
  width: 100%;
  height: 50px;
}
.buttons {
  color: #ff0b37;
  border-radius: 3px;
  border-width: 2px;
  border-color: #ff0b37;
  border-style: solid;
  height: 25px;
  width: 120px;
  background-color: transparent;
}
.heading {
  color: black;
  font-size: 15px;
  font-family: 'roboto';
}
