/* Write your CSS here */
.mainBackground {
  background-image: linear-gradient(rgb(56, 172, 211), white);
  height: 100vh;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding: 20px;
}
.subBackground {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  width: 100%;
  flex-wrap: wrap;
  padding: 18px;
}
.headings {
  font-family: 'roboto';
  font-size: 30px;
  color: rgb(21, 113, 144);
}
.inputed {
  border-radius: 8px;
  border-color: #000000;
  width: 50%;
  margin-right: 10px;
}
.created {
  background-color: rgb(7, 54, 70);
  border-width: 0px;
  border-radius: 8px;
  width: 50%;
  color: #ffffff;
}
.ins {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
